// ÐÐ¼Ð¿Ð¾ÑÑÐ¸ÑÑÐµÐ¼ Ð²ÑÐµ Ð¾Ð¿ÐµÑÐ°ÑÐ¾ÑÑ Ð´Ð»Ñ ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ñ Ð¾Ð±ÑÐµÐ³Ð¾ Ð¾Ð±ÑÐµÐºÑÐ°
import { logicalOperators } from './logical'
import { elementOperators } from './element'
import { arrayOperators } from './array'
import { evaluationOperators } from './evaluation'
import { bitwiseOperators } from './bitwise'
import { textSearchOperators } from './text'
import { comparisonOperators } from './comparison'
import { QueryOperator, QueryValue, QueryOperatorError } from './types'

// ÐÐ±ÑÐµÐ´Ð¸Ð½ÑÐµÐ¼ Ð²ÑÐµ Ð¾Ð¿ÐµÑÐ°ÑÐ¾ÑÑ Ð² Ð¾Ð´Ð¸Ð½ Ð¾Ð±ÑÐµÐºÑ
export const allOperators = {
  ...logicalOperators,
  ...elementOperators,
  ...arrayOperators,
  ...evaluationOperators,
  ...bitwiseOperators,
  ...textSearchOperators,
  ...comparisonOperators,
} as const

// Ð¢Ð¸Ð¿ Ð´Ð»Ñ Ð²ÑÐµÑ Ð´Ð¾ÑÑÑÐ¿Ð½ÑÑ Ð¾Ð¿ÐµÑÐ°ÑÐ¾ÑÐ¾Ð²
export type OperatorType = keyof typeof allOperators

// Ð¢Ð¸Ð¿ Ð´Ð»Ñ ÐºÐ¾Ð½ÑÑÑÑÐºÑÐ¾ÑÐ° Ð¾Ð¿ÐµÑÐ°ÑÐ¾ÑÐ°
export type OperatorConstructor = new (value: QueryValue) => QueryOperator

// Ð¤ÑÐ½ÐºÑÐ¸Ñ Ð´Ð»Ñ Ð¿ÑÐ¾Ð²ÐµÑÐºÐ¸, ÑÐ²Ð»ÑÐµÑÑÑ Ð»Ð¸ ÑÑÑÐ¾ÐºÐ° Ð¾Ð¿ÐµÑÐ°ÑÐ¾ÑÐ¾Ð¼
export function isOperator(value: string): value is OperatorType {
  return value in allOperators
}

// Ð¤ÑÐ½ÐºÑÐ¸Ñ Ð´Ð»Ñ ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ñ Ð¾Ð¿ÐµÑÐ°ÑÐ¾ÑÐ° Ð¿Ð¾ Ð¸Ð¼ÐµÐ½Ð¸
export function createOperator(
  operator: OperatorType,
  value: QueryValue,
): QueryOperator {
  const OperatorClass = allOperators[operator] as OperatorConstructor
  return new OperatorClass(value)
}

// Ð¤ÑÐ½ÐºÑÐ¸Ñ Ð´Ð»Ñ Ð²Ð°Ð»Ð¸Ð´Ð°ÑÐ¸Ð¸ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ñ Ð¾Ð¿ÐµÑÐ°ÑÐ¾ÑÐ°
export function validateOperatorValue(
  operator: OperatorType,
  value: unknown,
): value is QueryValue {
  try {
    createOperator(operator, value as QueryValue)
    return true
  } catch (error) {
    if (error instanceof QueryOperatorError) {
      return false
    }
    throw error
  }
}

// Ð¤ÑÐ½ÐºÑÐ¸Ñ Ð´Ð»Ñ Ð¿Ð¾Ð»ÑÑÐµÐ½Ð¸Ñ ÑÐ¸Ð¿Ð° Ð¾Ð¿ÐµÑÐ°ÑÐ¾ÑÐ°
export function getOperatorType(operator: QueryOperator): string {
  return operator.type
}

// Ð¤ÑÐ½ÐºÑÐ¸Ñ Ð´Ð»Ñ Ð¿ÑÐ¾Ð²ÐµÑÐºÐ¸, ÑÐ²Ð»ÑÐµÑÑÑ Ð»Ð¸ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ Ð¾Ð¿ÐµÑÐ°ÑÐ¾ÑÐ¾Ð¼ Ð¾Ð¿ÑÐµÐ´ÐµÐ»ÐµÐ½Ð½Ð¾Ð³Ð¾ ÑÐ¸Ð¿Ð°
export function isOperatorOfType<T extends QueryOperator>(
  value: unknown,
  type: string,
): value is T {
  return (
    value !== null &&
    typeof value === 'object' &&
    'type' in value &&
    (value as any).type === type
  )
}
