import { ValueType, BPlusTree } from 'b-pl-tree'
import { StoredIList } from '../types/StoredIList'
import { Item } from '../types/Item'
import fs from 'fs-extra'
import pathlib from 'path'
import { IList } from '../IList'
import Collection from '../collection'
import { entity_delete } from '../utils/entity_delete'
import { entity_update } from '../utils/entity_update'
import { IStoredRecord } from '../types/IStoredRecord'
import { entity_create } from '../utils/entity_create'
import { is_stored_record } from '../utils/is_stored_record'
import { cloneDeep } from 'lodash-es'
import { fromZodError } from 'zod-validation-error'
import { cloneBPlusTree, serializeBPlusTree, deserializeBPlusTree } from '../utils/btree-serialization'

export class FileStorage<T extends Item>
  implements IList<T>
{
  get name() {
    return 'FileStorage' as const
  }
  singlefile: boolean = false

  //  ÑÑÐ°Ð½Ð¸ÑÑ Ð¿ÑÐ¾Ð¼Ð¸ÑÑ ÑÐ¸Ð¿Ð° ÐºÑÑÐ¸ÑÐ¾Ð²Ð°Ð½Ð¸Ðµ Ð´Ð°Ð½Ð½ÑÑ Ðº ÐºÐ¾ÑÐ¾ÑÑÐ¼ Ð±ÑÐ» Ð´Ð¾ÑÑÑÐ¿, Ð¸ ÐºÐ¾ÑÐ¾ÑÑÐµ Ð½Ðµ Ð¾Ð±Ð½Ð¾Ð²Ð»ÑÐ»Ð¸ÑÑ
  // Ð° Ð½Ð° Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ðµ Ð²ÑÑÑÐ°Ð²Ð»ÑÑÑ Ð½Ð¾Ð²ÑÐ¹ Ð¿ÑÐ¾Ð¼Ð¸Ñ
  // ÑÐ°ÐºÐ¸Ð¼ Ð¾Ð±ÑÐ°Ð·Ð¾Ð¼ Ð´Ð°Ð½Ð½ÑÐµ Ð²ÑÐµÐ³Ð´Ð° Ð±ÑÐ´ÑÑ ÑÐ²ÐµÐ¶Ð¸Ð¼Ð¸... ÐµÑÐ»Ð¸ Ð½ÐµÑ Ð´ÑÑÐ³Ð¾Ð³Ð¾ ÑÐ¸ÑÐ°ÑÐµÐ»Ñ Ð¿Ð¸ÑÐ°ÑÐµÐ»Ñ ÑÐ°Ð¹Ð»Ð¾Ð²
  // Ð¼Ð¾Ð¶Ð½Ð¾ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÑ Ð±Ð¸Ð±Ð»Ð¸Ð¾ÑÐµÐºÑ Ð´Ð»Ñ Ð¼Ð¾Ð½Ð¸ÑÑÐ¾Ð¸Ð½Ð³Ð° Ð·Ð° ÑÐ°Ð¹Ð»Ð°Ð¼Ð¸
  tree: BPlusTree<string, ValueType> = new BPlusTree(32, true)
  get folder(): string {
    return pathlib.join(this.collection.root, this.collection.name)
  }
  private keyField?: string
  constructor(keyField?: string) {
    this.keyField = keyField
  }
  exists!: Promise<boolean>
  collection!: Collection<T>
  construct() {
    return new FileStorage<T>()
  }

  init(collection: Collection<T>): IList<T> {
    this.collection = collection
    if (this.keyField && !this.collection.indexDefs[this.keyField].unique) {
      throw new Error(`key field ${this.keyField} is not unique`)
    }
    this.exists = fs
      .ensureDir(this.folder)
      .then((_) => true)
      .catch((_) => false)
    return this
  }
  async clone(): Promise<IList<T>> {
    if (await this.exists) {
      const res = new FileStorage<T>()
      res.tree = cloneBPlusTree(this.tree)
      return res
    }
    throw new Error('folder not found')
  }
  persist(): StoredIList {
    return {
      keyField: this.keyField,
      counter: this._counter,
      tree: serializeBPlusTree(this.tree),
    }
  }

  load(obj: StoredIList): IList<T> {
    this._counter = obj.counter
    // prefer name that in configuration
    this.keyField = !obj.keyField
      ? this.keyField
      : this.keyField
      ? this.keyField
      : obj.keyField
    this.tree = deserializeBPlusTree(obj.tree)
    return this
  }

  get forward() {
    return {
      [Symbol.asyncIterator]: () => this.toArray(),
    }
  }

  get backward() {
    return {
      [Symbol.asyncIterator]: () => this.toArrayReverse(),
    }
  }

  async *toArray() {
    const res = await this.exists
    if (res) {
      const it = this.tree.each()(this.tree)
      let cursor = it.next()
      while (!cursor.done && cursor.value) {
        yield await fs.readJSON(this.get_path(cursor.value.value))
        cursor = it.next()
      }
    } else throw new Error('folder not found')
  }

  async *toArrayReverse() {
    if (await this.exists) {
      const it = this.tree.each(false)(this.tree)
      let cursor = it.next()
      while (!cursor.done && cursor.value) {
        yield await fs.readJSON(this.get_path(cursor.value.value))
        cursor = it.next()
      }
    } else throw new Error('folder not found')
  }

  private key_filename(key: ValueType) {
    return `${key?.toString() ?? 'undefined'}.json`
  }

  private set_path(key: ValueType) {
    return pathlib.join(this.folder, this.key_filename(key))
  }

  private get_path(value: string) {
    return pathlib.join(this.folder, value)
  }

  async reset(): Promise<void> {
    if (await this.exists) {
      await fs.remove(this.folder)
      this.tree.reset()
      this.exists = fs
        .ensureDir(this.folder)
        .then((_) => true)
        .catch((_) => false)
    } else throw new Error('folder not found')
  }

  async get(key: ValueType): Promise<T | undefined> {
    if (await this.exists) {
      const value = this.tree.findFirst(key)
      if (value) {
        const location = this.get_path(value)
        const result: T | IStoredRecord<T> = await fs.readJSON(location)
        if (is_stored_record(result)) {
          if (!this.collection.audit) {
            await fs.writeJSON(location, result)
          }
          return result.data
        } else {
          return result
        }
      }
    }
    throw new Error('folder not found')
  }

  async set(key: ValueType, item: T): Promise<T> {
    if (await this.exists) {
      let valiadtor = this.collection.validator(item)
      if (valiadtor.success) {
        this._counter++
        // checkif exists
        // Ð±ÐµÑÐµÐ¼ Ð½Ð¾Ð²ÑÐ¹ ÐºÐ»ÑÑ
        const uid = this.keyField
          ? item[this.keyField]
            ? item[this.keyField]
            : key
          : key

        // Ð¿Ð¸ÑÐµÐ¼ Ð² ÑÐ°Ð¹Ð»

        let result: T | IStoredRecord<T>
        if (this.collection.audit) {
          result = entity_create(
            item[this.collection.id],
            cloneDeep(item),
            this.collection.validation,
          )
        } else {
          result = cloneDeep(item)
        }

        await fs.writeJSON(this.set_path(uid), result)
        // Ð²ÑÑÐ°Ð²Ð»ÑÐµÐ¼ Ð² ÑÑÐ°Ð½Ð¸Ð»Ð¸ÑÐµ
        this.tree.insert(key, this.key_filename(uid))
        return this.collection.audit ? (result as IStoredRecord<T>).data : (result as T)
      } else {
        console.log(fromZodError((valiadtor as any).errors))
        throw new Error('Validation error')
      }
    }
    throw new Error('folder not found')
  }

  async update(key: ValueType, item: T): Promise<T> {
    // checkif exists
    if (await this.exists) {
      let valiadtor = this.collection.validator(item)
      if (valiadtor.success) {
        // Ð¸ÑÐµÐ¼ ÑÐµÐºÑÑÐµÐµ Ð½Ð°Ð·Ð²Ð°Ð½Ð¸Ðµ ÑÐ°Ð¹Ð»Ð°
        const location = this.get_path(this.tree.findFirst(key))
        let result: T = item

        const record = (await fs.readJSON(location)) as T
        if (this.collection.audit) {
          let res: T | IStoredRecord<T>
          if (!is_stored_record(record)) {
            res = entity_create(
              item[this.collection.id],
              cloneDeep(item),
              this.collection.validation,
            )
          } else {
            res = entity_update<T>(record, cloneDeep(item))
          }
          result = res.data
          await fs.writeJSON(location, res)
        } else {
          // Ð·Ð°Ð¿Ð¸ÑÑÐ²Ð°ÐµÐ¼ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ Ð² ÑÐ°Ð¹Ð»
          await fs.writeJSON(location, result)
        }
        return result
      } else {
        console.log(fromZodError((valiadtor as any).errors))
        throw new Error('Validation error')
      }
    }
    throw new Error('folder not found')
  }

  async delete(key: ValueType): Promise<T> {
    if (await this.exists) {
      const value = this.tree.findFirst(key)
      if (value) {
        const location = this.get_path(value)
        const item = await fs.readJSON(location)
        let result: T
        if (is_stored_record<T>(item)) {
          result = item.data
          const res = entity_delete(item)
          await fs.writeJSON(location, res)
        } else {
          result = item
          await fs.unlink(location)
        }
        this.tree.remove(key)
        return result
      }
    }
    throw new Error('folder not found')
  }

  _counter: number = 0
  get counter(): number {
    return this._counter
  }
  get length(): number {
    return this.tree.size
  }
}
